import './Instructions.css'

const instData = [{name: " 1", content: "  1"}, {name: " 2", content: <div style={{backgroundColor:"red", width:20, height:20}}></div>}]
const qData = [
  { name: " ", 
    content: <>     ,       ,   ,    </>
  }, 
  { name: " ", 
    content: <>
                ,         <br/>
      1)    ,        ,      <br/>
      2)        ,        . 
          ,  ,     ,    , 
                 <br/>
      3)              ,             
    </>
  }, 
  { name: "   ", 
    content: <>
         ,        ,         <br/>
      2)      ,         ,    "  ",
              ,    .  1      
    </>
  }
]

function build(data) {
  let output = []
  for (let i in data) {
    output.push(
    <div key={i} className="instruction" >
      <div className="instruction_header" onClick={(e) => {
        let active = [...e.target.closest('div:has(.instruction)').getElementsByClassName('instruction active')]
        e.target.closest('.instruction').classList.toggle('active')
        for (let i in active) {
          active[i].classList.remove('active')
        }
      }}>{data[i].name}</div>
      <div className="folding_content">
        <div className="instruction_content">{data[i].content}</div>
      </div>
    </div>
  )}
  return output
}

function Instructions() {
  return(
    <div className="instructions">
      {build(instData)}
    </div>
  )
}

function Questions() {
  return(
    <div className="questions">
      {build(qData)}
    </div>
  )
}

export { Instructions, Questions }